const Skills = () => {
  const [abouts, setAbouts] = useState([])
  useEffect(() => {
    const query = '*[_type == "abouts"]'

    client.fetch(query)
      .then((date) => setAbouts(date))
  }, []);


  return (
    <>
      <h2 className='head-text'>
        I'm a 
        <span> Exquisite Designer </span>
        and
        <span>  a Systematic Solution Provider </span>
      </h2>
      <div className='app__profiles'>
        {abouts.map((abouts, index) => (
          <motion.div
            whileInView={{ opacity: 1 }}
            whileHover={{ scale: 1.1 }}
            transition={{ duration: 0.5, type: 'tween' }}
            className='app__profile-item'
            key={abouts.title + index}
          >
            <img src={urlFor(abouts.imgUrl)} alt={abouts.title} />
            <h2 className="bold-text" style={{ marginTop: 20 }}>{abouts.title}</h2>
            <p className="p-text" style={{ marginTop: 10 }}>{abouts.description}</p>
          </motion.div>
        ))}
      </div>
    </>
  )
}
export default AppWrap(
  MotionWrap(Skills, 'app__about'),
  'about',
  'app__whitebg',
);
