import React, { useState, useEffect } from 'react'

import { motion } from 'framer-motion'
import './About.scss'
// import { HiChevronLeft, HiChevronRight } from 'react-icons/hi';

import { AppWrap, MotionWrap } from '../../wrapper';
import { urlFor, client } from '../../client';



function About(){
  const [currentIndex, setCurrentIndex] = useState(0);
  const [aboutme, setaboutme] = useState([]);
  const [brands, setBrands] = useState([]);

  const handleClick = (index) => {
    setCurrentIndex(index);
  };

  useEffect(() => {
    const query = '*[_type == "aboutme"]';
    const brandsQuery = '*[_type == "brands"]';

    client.fetch(query).then((data) => {
        setaboutme(data);
    });

    client.fetch(brandsQuery).then((data) => {
      setBrands(data);
    });
  }, []);

  return (
    <>
      {aboutme.length && (
        <>
          <div className="app__testimonial-item app__flex">
            {/* <img src={urlFor(testimonials[currentIndex].imgurl)} alt={testimonials[currentIndex].name} /> */}
            {/* <h4 className="bold-text">{testimonials[currentIndex].name}</h4> */}
            <div className="app__testimonial-content">
              <div>
              <h4 className="bold-text">{aboutme[currentIndex].name}</h4>
                {/* <h5 className="p-text">{testimonials[currentIndex].company}</h5> */}
                <p className="p-text">{aboutme[currentIndex].feedback}</p>
              </div>
            </div>
          </div>

          {/* <div className="app__testimonial-btns app__flex">
            <div className="app__flex" onClick={() => handleClick(currentIndex === 0 ? testimonials2.length - 1 : currentIndex - 1)}>
              <HiChevronLeft />
            </div>

            <div className="app__flex" onClick={() => handleClick(currentIndex === testimonials2.length - 1 ? 0 : currentIndex + 1)}>
              <HiChevronRight />
            </div>
          </div> */}
        </>
      )}

      <div className="app__testimonial-brands app__flex">
        {brands.map((brand) => (
          <motion.div
            whileInView={{ opacity: [0, 1] }}
            transition={{ duration: 0.5, type: 'tween' }}
            key={brand._id}
          >
            <img src={urlFor(brand.imgUrl)} alt={brand.name} />
          </motion.div>
        ))}
      </div>
    </>
  );
};
export default AppWrap(
  MotionWrap(About, 'app__about'),
  'about',
  // 'app__whitebg',
);
// export default About